:root {
    --grass: #5FBD58;
    --bug: #92BC2C;
    --dark: #595761;
    --dragon: #0C69C8;
    --electric: #F2D94E;
    --fairy: #EE90E6;
    --fighting: #D3425F;
    --fire: #dc872f;
    --flying: #A1BBEC;
    --ghost: #5F6DBC;
    --ground: #DA7C4D;
    --ice: #75D0C1;
    --normal: #A0A29F;
    --poison: #B763CF;
    --psychic: #ff2ca8;
    --rock: #a38c21;
    --steel: #5695A3;
    --water: #539DDF;
}

.imageContainer {
    height: 150px;
    width: 150px;
    object-fit: contain;
    display: flex;
    justify-content: center;
    margin-top: auto;
}

.imgType {
    height: 10;
    object-fit: contain;
}

.thumbnailContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.thumbnailContainer img {
    width: 120px;
    height: 120px;
    object-fit: contain;
}

.pokemonName {
    font-family: 'Press Start 2P', cursive;
}


/* Icone do tipo dos Pokemon INICIO */

.wrapper {
    width: 800px;
    margin: auto auto;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(6, 1fr);
    align-content: center;
    align-items: center;
}

.icon {
    border-radius: 100%;
    height: 100px;
    width: 100px;
    margin: auto;
    transition: 200ms all;
}

.icon:hover {
    filter: saturate(200%);
    transform: scale(1.1);
    cursor: pointer;
}

.icon img {
    height: 60%;
    width: 60%;
    margin: 20%;
}

.grass {
    background: var(--grass);
    box-shadow: 0 0 20px var(--grass);
}

