.pokeType {
    display: flex;
    display: -webkit-flex;
    align-items: center;
    grid-gap: 0 10px;
    grid-gap: 0 20px;
    gap: 0 20px;
    -webkit-justify-content: center;
    justify-content: center;
    margin-top: 30px;
}

.pokeTypeBg>img {
    width: 20px;
    height: 20px;
}

.pokeTypeBg {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-content: center;
    align-content: center;
    -webkit-align-items: center;
    align-items: center;
}

/* Estilize o tooltip */
.pokeTypeInfo {
    position: relative;
    cursor: pointer;
}

/* Estilize o texto do tooltip */
.pokeTypeInfo .pokeTypeText {
    visibility: hidden;
    width: 120px;
    background-color: #333;
    color: #fff;
    text-align: center;
    border-radius: 5px;
    padding: 5px;
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
    transition: opacity 0.3s;
}

/* Exiba o tooltip quando passar o mouse sobre o elemento pai */
.pokeTypeInfo:hover .pokeTypeText {
    visibility: visible;
    opacity: 1;
}